html, body, #root {
  width: 100vw;
  height: 100vh;
}
.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: white;
}

form {
  display: flex;
  flex-direction: column;
  min-width: min(90%, 450px);
}

.row {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 90px;
  gap: 10px;
}

.row label {
  width: 70px;
  font-weight: 500;
  color: #5f5f5f
}

.row input {
  width: 200px;
  height: 50px;
  border-radius: 10px;
  border: solid 1px #d2d2d2;
  text-align: center;
  font-size: 16px; 
  margin-left: 10%;
}

.row input:focus, .row input:hover {
  border: solid 3px #0479D3;
  outline: none;
}

.submitBtn {
  background-color: #4aacda;
  color: white;
  border: none;
  border-radius: 10px;
  height: 40px;
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 18px;
  box-shadow: 0 3px 3px rgb(0 0 0 / 0.1);
}

.submitBtn:hover {
  box-shadow: none;
  opacity: 0.9;
}

h1 {
  font-family: 'Dongle', sans-serif;
  font-size: 40px;
}

@media (min-aspect-ratio: 9/10) {

  body {
    background-image: url("./assets/background.jpeg");
    background-size: cover;
    background-position: center;
    position: relative;
    background-repeat: no-repeat;
  }

  .container {
    width: min(100vw, 600px);
    height: max(70vh, 700px);
    border-radius: 20px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
  }

  .row label {
    width: min(30%, 10vw);
  }

  .row input {
    margin-left: 15%;
    font-size: 16;
  }

  form {
    width: 70%;
  }
  
}
